import Vue from 'vue'
import Router from 'vue-router'
import Tareas from '@/components/Tareas'
import AddTarea from '@/components/AddTarea'
import Tarea from '@/components/Tarea'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',name:'Tareas',component: Tareas},
    // {path: '/',component: Tareas,children:[{path: '',name: 'Tareas',components:{default:Tarea}}]},
    // {path: '/add',name: 'AddTarea',component: AddTarea},
  ]
})



