<template>
<v-container>
  <v-btn @click="handleTarea" v-show="!newTarea" round primary dark>Agregar tarea</v-btn>
  <AddTarea v-show="newTarea"></AddTarea>
  Tareas completadas
  <Tarea v-for="tarea in tareasCompletadas" :key="tarea.key" :tarea="tarea"></Tarea>
  Tareas no completadas
  <Tarea v-for="tarea in tareasNoCompletadas" :key="tarea.key" :tarea="tarea"></Tarea>
</v-container>
</template>
<script>
import Tarea from './Tarea.vue'
import AddTarea from './AddTarea.vue'
import {
  mapState,
  mapActions,
  mapGetters,
  mapMutations
} from 'vuex'
export default {
  name: "Tareas",
  data: () => ({}),
  computed: { ...mapState(['tareas', 'newTarea']),
    ...mapGetters(['tareasCompletadas', 'tareasNoCompletadas'])
  },
  methods: { ...mapMutations(['handleTarea'])
  },
  components: {
    Tarea,
    AddTarea
  },
}
</script>
<style lang="" scoped>
</style>



