import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state:{
    nombre: 'Luis Bernardo García López',
    tareas:[{nombre:'Hacer un todo en Vue', status:true},{nombre:'Hacer el mandado', status:false},
    {nombre:'Hacer la comida', status:false}],
    newTarea:false
  },
  getters:{
    tareasCompletadas:(state)=>{return state.tareas.filter((tarea)=>tarea.status)},
    tareasNoCompletadas:(state)=>{return state.tareas.filter((tarea)=>!tarea.status)},
  },
  mutations:{
    toggleTarea(state, tarea){tarea.status = !tarea.status;},
    handleTarea(state){state.newTarea = !state.newTarea;},
    addTarea(state, tarea){state.tareas.push(tarea); state.newTarea = !state.newTarea},
  },
  actions:{
    addTarea({commit}, tarea){commit('addTarea', tarea)}
  }
})



