<template>
  <v-container>
  <h1>Agregando tarea...</h1>
  <v-text-field name="input-1" label="Nombre de la tarea" id="testing" v-model="nombre"></v-text-field >
  <v-btn btn rounded dark @click="add()">Agregar</v-btn>
</v-container>
</template>

<script>
import {mapState, mapActions} from 'vuex'
export default {
  name:'AddTarea',
  data: () => ({nombre: ''}),
  computed:{...mapState(['newTarea']),},
  methods:{...mapActions(['addTarea']),
  add(){this.addTarea({nombre:this.nombre, status:false});this.tarea = '';}}
}
</script>

<style lang="css">
</style>



